import { Box, Stack, StackProps, VStack } from '@chakra-ui/react';
import * as React from 'react';

interface FeatureProps extends StackProps {
  children: React.ReactNode;
  ref?: React.RefObject<HTMLDivElement>;
  size?: 'full';
}

export const PageBlock = ({ children, size, ...rest }: FeatureProps) => {
  return (
    <VStack width="100%" m={size == 'full' ? undefined : [6, 12, 16]} align="start" spacing={5} {...rest}>
      <Box alignSelf="center" maxWidth={size == 'full' ? undefined : 800}>
        {children}
      </Box>
    </VStack>
  );
};
